import React, { useState, useEffect } from 'react';
import {url} from './url.js';
import './FrontHistorique/assets/fonts/fontawesome-all.min.css'
import './FrontHistorique/assets/bootstrap/css/bootstrap.min.css'
function Historique(){
    const[enchere,setEnchere]=useState([])
    useEffect(()=>{
        fetch(url+'historique')
            .then((response)=>response.json())
            .then((data)=>setEnchere(data))
    },[] );
return(
<body id="page-top">
    <div id="wrapper">
        <nav class="navbar navbar-dark align-items-start sidebar sidebar-dark accordion bg-gradient-primary p-0">
            <div class="container-fluid d-flex flex-column p-0"><a class="navbar-brand d-flex justify-content-center align-items-center sidebar-brand m-0" href="#">
                    <div class="sidebar-brand-icon rotate-n-15"><i class="fas fa-laugh-wink"></i></div>
                    <div class="sidebar-brand-text mx-3"><span>Enchere</span></div>
                </a>
                <hr class="sidebar-divider my-0"/>
                <ul class="navbar-nav text-light" id="accordionSidebar">
                    <li class="nav-item"><a class="nav-link" href="/stats"><i class="fas fa-tachometer-alt"></i><span>Statistiques</span></a></li>
                    <li class="nav-item"></li>
                    <li class="nav-item"></li>
                    <li class="nav-item"><a class="nav-link" href="/categorie"><i class="fas fa-user-circle"></i><span>Categorie</span></a></li>
                    <li class="nav-item"><a class="nav-link " href="/encours"><i class="fas fa-table"></i><span>Les encheres en cours</span></a><a class="nav-link active" href="/historique"><i class="fas fa-table"></i><span>Historique des encheres</span></a><a class="nav-link" href="/parametre"><i class="far fa-user-circle"></i><span>Parametre&nbsp;</span></a><a class="nav-link" href="/"><span>Se deconnecter</span></a></li>
                </ul>
                <div class="text-center d-none d-md-inline"></div><a href="index.html"></a>
            </div>
        </nav>
        <div class="d-flex flex-column" id="content-wrapper">
            <div id="content">
                <div class="container-fluid">
                    <div class="card shadow">
                        <div class="card-header py-3">
                            <p class="text-primary m-0 fw-bold">Historique des encheres</p>
                        </div>
                        <div class="card-body">
                            <div class="table-responsive table mt-2" id="dataTable" role="grid" aria-describedby="dataTable_info">
                                <table class="table my-0" id="dataTable"/>
                                    <thead>
                                        <tr>
                                            <th>Produit</th>
                                            <th>Proprietaire</th>
                                            <th>Categorie</th>
                                            <th><center>Start date</center></th>
                                            <th>Prix final</th>
                                            <th>Gagnant</th>
                                        </tr>
                                    </thead>
                                   
                                    <tbody>
                                        {Array.isArray(enchere) ?enchere.map((liste)=>(
                                        <tr>
                                            
                                            <td><img className="rounded-circle me-2" width="30" height="30" src="./FrontHistorique/assets/img/avatars/avatar1.jpeg"/>{liste.nom}</td>
                                            <td>{liste.util.nom} {liste.util.prenom}</td>
                                            <td>{liste.categ.libelle}</td>
                                            <td>{liste.debut}</td>
                                            <td>AR {liste.price}</td>
                                            <td>{liste.gagnant.nom} {liste.gagnant.prenom}</td>
                                        </tr>
                                       )):"pas d'enchere conclu"}
                                    </tbody>
                                    <tfoot>
                                        <tr></tr>
                                    </tfoot>
                                <table/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <footer class="bg-white sticky-footer">
                <div class="container my-auto">
                    <div class="text-center my-auto copyright"><span>Copyright Â© Brand 2023</span></div>
                </div>
            </footer>
        </div><a class="border rounded d-inline scroll-to-top" href="#page-top"><i class="fas fa-angle-up"></i></a>
    </div>
    <script src="./FrontHistorique/assets/bootstrap/js/bootstrap.min.js"></script>
    <script src="./FrontHistorique/assets/js/bs-init.js"></script>
    <script src="./FrontHistorique/assets/js/theme.js"></script>
</body>
);

}
export default Historique;