import {Routes , Route } from 'react-router-dom' 
import Login from './Login'
import React from 'react';
import Statistiques from './Statistiques';
import ListeEncours from './ListeEncours';
import Historique from './Historique';
import Categ from './Categ';
import Parametre from './Parametre';
import Mouvement from './Mouvement';
import Demande from './Demande';
import Test from './Test';
function App() {
  return (
    <div>
      <Routes>
        <Route exact path='' element={<Login/>}></Route>
        <Route exact path='stats' element={<Statistiques/>}></Route>
        <Route exact path='encours' element={<ListeEncours/>}></Route>
        <Route exact path='historique' element={<Historique/>}></Route>
        <Route exact path='categorie' element={<Categ/>}></Route>
        <Route exact path='parametre' element={<Parametre/>}></Route>
        <Route exact path='mouv' element={<Mouvement/>}></Route>
        <Route exact path='demande' element={<Demande/>}></Route>
        <Route exact path='test' element={<Test/>}></Route>
      </Routes>
    </div>
  );
}

export default App;
